import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { AuthService } from '../auth.service';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-change-password-modal-content',
  templateUrl: './change-password-modal-content.component.html',
  styleUrls: ['./change-password-modal-content.component.scss']
})
export class ChangePasswordModalContentComponent implements OnInit {

  logUser: any = {password: "", newpassword: "", repassword: ""};
  showAlert: boolean = false;
  activeUser: any = null;

  constructor(
    public activeModal: NgbActiveModal,
    private authService: AuthService,
    private apiService: ApiService,
  ) { 
    this.authService.afAuth.authState.subscribe(currentUser => {
      this.activeUser = currentUser;
    });
  }

  ngOnInit() {
  }

  changePassword() {
    if(this.logUser.newpassword == this.logUser.repassword && this.activeUser){
      this.showAlert = false;
      //this.authService.updatePassword(this.logUser.newpassword);
      console.log('Se ha enviado un correo a ' + this.activeUser.email);
      this.authService.sendMailResetPassword(this.activeUser.email)
      .then(() => {
        console.log('Finalizo el correo');
      });
    } else {
      this.showAlert = true;
    }
  }

}



