import { Component, OnInit } from '@angular/core';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { RegisterData2Component } from '../register-data2/register-data2.component';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-register-data1',
  templateUrl: './register-data1.component.html',
  styleUrls: ['./register-data1.component.scss']
})
export class RegisterData1Component implements OnInit {

  icon_man: boolean = false; 
  icon_woman: boolean = false;
  name: string;
  email: string;
  weight: number;
  weightUnit: number = 1;
  genderId: number = 1;
  height: number;
  heightUnit: number = 1;
  operator: number;
  objUser: any;
  showAlert: string;
  catWeight: any = [];
  catHeight: any = [];
  catGender: any = [];
  foots: number;
  inches: number;

  constructor(
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private apiService: ApiService,
  ) { 

  }

  ngOnInit() {
    this.loadCatWeight();
    this.loadCatHeight();
    this.loadCatGender();
  }

  loadCatWeight() {
    this.apiService.getUnitWeight()
    .then(data => {
      this.catWeight = data;
    });
  }

  loadCatHeight() {
    this.apiService.getUnitHeight()
    .then(data => {
      this.catHeight = data;
    });
  }

  loadCatGender() {
    this.apiService.getGenderType()
    .then(data => {
      this.catGender = data;
    });
  }

  validateForm():boolean {
    this.showAlert = null;
    let valid = true;
    if(!this.genderId || !this.name || !this.email || !this.weight || !this.weightUnit
    || !(this.height || this.foots || this.inches) || !this.heightUnit || !this.operator) {
      this.showAlert = "Debe completar el formulario.";
      valid = false;
    }
    return valid;
  }

  openModalRegister2(){
    if(this.validateForm()) {
      this.activeModal.close('Close click');
      this.objUser = {name: this.name, email: this.email, gender: this.genderId, weight: this.weight, height: 
        typeof this.height == undefined || this.height == null? typeof this.foots == undefined || this.foots == null ? 
        this.inches : this.foots : this.height, weightUnit: this.weightUnit, heightUnit: this.heightUnit, 
        operator: this.operator};
      let modalRef = this.modalService.open(RegisterData2Component);
      modalRef.componentInstance.setObjUser(this.objUser);
    }
  }

  changeClass(event) {
    if(!event.target.classList.contains('icon-selected'))
    {
      event.target.classList.add('icon-selected');
    }
  }

  setOperator(id:number){
    this.operator = id;
  }  

}



