import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AdminPlanFrmModalComponent } from '../admin-plan-frm-modal/admin-plan-frm-modal.component';

@Component({
  selector: 'app-admin-plan',
  templateUrl: './admin-plan.component.html',
  styleUrls: ['./admin-plan.component.scss']
})
export class AdminPlanComponent implements OnInit {

  listPlans: any;
  loader:boolean = false;

  constructor(
    private apiService: ApiService,
    private modalService: NgbModal,
  ) { }

  ngOnInit() {
    this.getPlans();
  }

  getPlans(){
    this.loader = true;
    this.apiService.getPlans().then(data => {
      this.listPlans = data;
      this.listPlans.forEach((plan, index) => {
        this.apiService.getImg(plan.url)
        .then(imageUrl => {
          this.listPlans[index].url = imageUrl;
        });
        this.loader = false;
      });
    }).catch(() => {
      this.loader = false;
    });
  }

  openModalFrm(planId) {
    let modalRef = this.modalService.open(AdminPlanFrmModalComponent);
    if(planId != 0) {
      modalRef.componentInstance.getPlan(planId);
    }
  }

  clickDelete(id: number) {
    if(confirm("Esta seguro que desea eliminar este plan? ")) {
      this.apiService.deletePlan(id);
    }
  }

}


