import { Component, OnInit } from '@angular/core';
import { User } from '../user';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthService } from '../auth.service';
import { ApiService } from '../api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  registerForm: FormGroup;
  alerts: any[] = [];
  frmSubmit: boolean = false;
  loader: boolean = false;
  recaptchaVerifier: any;
  errorSendTxt:boolean = false;
  countries: any[] = [{code:'co', ind:'+57', country: 'Colombia'}, {code:'do', ind: +'+001', country: 'República Dominicana'}];
  gender: any = [];
  confirmationResult: any;
  validCode: string;
  validForm: boolean = true;
  errorvalidCode: boolean = false;
  regUser: User = {email: "", password: "", name: "", phone: "", country: this.countries[0].ind, gender: 3};
  repassword: string = "";
  weight: any = [];
  activeUser: any = null;

  constructor(
    private authService: AuthService,
    private apiService: ApiService,
    private router: Router,
  ) {
    this.authService.afAuth.authState.subscribe(currentUser => {
      this.activeUser = currentUser;
    });
  }

  ngOnInit() {
    this.registerForm = new FormGroup({
      'name': new FormControl(this.regUser.name),
      'country': new FormControl(this.regUser.country),
      'email': new FormControl(this.regUser.email),
      'phone': new FormControl(this.regUser.phone),
      'gender': new FormControl(this.regUser.gender),
      'height': new FormControl(this.regUser.height),
      'age': new FormControl(this.regUser.age),
      'weight': new FormControl(this.regUser.weight),
      'password': new FormControl(this.regUser.password),
      'repassword': new FormControl(this.regUser.password),
    });
    this.getGender();
    this.getWeight();
  }

  getGender(){
    this.apiService.getGenderType()
    .then(data => {
      this.gender = data;
    }).catch(() => {
      this.gender.push({id: 1, name: 'Hombre'});
      this.gender.push({id: 2, name: 'Mujer'});
      this.gender.push({id: 3, name: 'Otro'});
    });
  }

  getWeight(){
    this.apiService.getUnitWeight()
    .then(data => {
      this.weight = data;
    }).catch(() => {
      this.weight.push({id: 1, name: 'LBS'});
      this.weight.push({id: 2, name: 'KGS'});
    });
  }

  initCaptcha(){
    this.authService.initCaptcha('frm-register-captcha')
    .then(captcha => {
      this.recaptchaVerifier = captcha;
    });
  }

  ngAfterViewInit(){
    this.initCaptcha();
  }

  validFrm(){
    if(!this.regUser.name)
      return false;
    if(!this.regUser.country)
      return false;
    if(!this.regUser.email)
      return false;
    if(!this.regUser.phone)
      return false;
    if(!this.regUser.gender)
      return false;
    if(!this.regUser.height)
      return false;
    if(!this.regUser.age)
      return false;
    if(!this.regUser.weight)
      return false;
    if(!this.regUser.password)
      return false;
    if(this.regUser.password != this.repassword)
      return false;
    return true;
  }

  register(){
    if(this.validFrm())
    {
      this.loader = true;
      let completeNumber = this.registerForm.controls.country.value + this.regUser.phone;
      console.log(completeNumber);
      this.authService.sendLoginCode(this.recaptchaVerifier, completeNumber)
      .then(data => {
        this.validForm = true;
        this.errorSendTxt = false;
        this.confirmationResult = data;
        this.loader = false;
        this.frmSubmit = true;
      }) 
      .catch(error => {
        this.loader = false;
        this.frmSubmit = false;
        this.errorSendTxt = true;
      });
    } else {
      this.validForm = false;
    }
  }

  validateCode(){
    this.loader = true;
    this.authService.verifyLoginCode(this.confirmationResult, this.validCode, this.regUser.name, this.regUser.email, this.regUser.password)
    .then(userFire => {
      this.errorvalidCode = false;
      this.apiService.createUser(userFire['uid'], userFire['phoneNumber'], userFire['displayName'], userFire['email'], "", 
        this.regUser.gender, this.regUser.weight, this.regUser.height, true, 1, "website", 1,1,1,0)
      .then(resultApi => {
        this.loader = false;
        this.router.navigate(['/my-account']);
      })
      .catch(errorApi => {
        this.loader = false;
      });
    }) 
    .catch(error => {
      this.errorvalidCode = true;
      this.loader = false;
    });
  }

  loginWithFacebook(){
    this.authService.linkFacebook()
    .then(userFire => {
      this.loader = true;
      this.apiService.createUser(userFire['uid'], userFire['phoneNumber'], userFire['displayName'], userFire['email'], "", 
        this.regUser.gender, this.regUser.weight, this.regUser.height, true, 0, "website", 1,1,1,0)
      .then(resultApi => {
        this.loader = false;
        this.router.navigate(['/my-account']);
      })
      .catch(errorApi => {
        this.loader = false;
      });
    });
  }

}



