import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AdminRoutinesFrmModalComponent } from '../admin-routines-frm-modal/admin-routines-frm-modal.component';

@Component({
  selector: 'app-admin-routines',
  templateUrl: './admin-routines.component.html',
  styleUrls: ['./admin-routines.component.scss']
})
export class AdminRoutinesComponent implements OnInit {

  listRoutines: any;
  loader:boolean = false;

  constructor(
    private apiService: ApiService,
    private modalService: NgbModal,
  ) { }

  ngOnInit() {
    this.getRoutines();
  }

  getRoutines(){
    this.loader = true;
    this.apiService.getRoutines().then(data => {
      this.listRoutines = data;
      this.listRoutines.forEach((routine, index) => {
        this.apiService.getImg(routine.urlImagen)
        .then(imageUrl => {
          this.listRoutines[index].urlImagen = imageUrl;
        });
      });
      this.loader = false;
    })
    .catch(() => {
      this.loader = false;
    });
  }

  openModalFrm(routineId) {
    let modalRef = this.modalService.open(AdminRoutinesFrmModalComponent);
    if(routineId != 0) {
      modalRef.componentInstance.getRoutine(routineId);
    }
  }

  clickDelete(id: number) {
    if(confirm("Esta seguro que desea eliminar esta rutina? ")) {
      console.log("Implement delete functionality here");
    }
  }

}



