import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../api.service';
import { DomSanitizer } from '@angular/platform-browser';
import { AuthService } from '../auth.service';

@Component({
  selector: 'app-influencer-page',
  templateUrl: './influencer-page.component.html',
  styleUrls: ['./influencer-page.component.scss']
})
export class InfluencerPageComponent implements OnInit {

  id: number;
  private sub: any;
  athlete: any = {listImg : ""};
  listPlans: any;
  activeUser: any = null;
  activePlan: any = {idPlan: -1};

  constructor(
    private route: ActivatedRoute,
    private apiService: ApiService,
    private sanitizer: DomSanitizer,
    private authService: AuthService,
  ) { 
    this.authService.afAuth.authState.subscribe(currentUser => {
      this.activeUser = currentUser;
      if(this.activeUser != null){
        if(typeof this.activeUser['uid'] != 'undefined'){
          this.getPlanUser();
        } else {
          this.activePlan = {idPlan: -1};
        }
      } else {
        this.activePlan = {idPlan: -1};
      }
    });
  }

  getParamns(){
    this.sub = this.route.params.subscribe(params => {
      this.id = +params['id'];
    });
  }

  getPlanUser() {
    this.apiService.getPlanProgressByUser(this.activeUser.uid)
    .then(result => {
      this.activePlan = result;
    });
  }

  getAthlete(){
    this.apiService.getAthleteById(this.id)
    .then(data => {
      this.athlete = data;
        this.apiService.getImg(this.athlete.url)
        .then(imageUrl => {
          this.athlete.url = imageUrl;
        });
        this.apiService.getImg(this.athlete.listImg)
        .then(imageUrl => {
          this.athlete.listImg = imageUrl;
        });
        this.athlete.videoPresentation = this.sanitizer.bypassSecurityTrustHtml(this.athlete.videoPresentation);
    });
  }

  getPlans(){
    this.apiService.getPlansByAthlete(this.id)
    .then(data => {
      this.listPlans = data;
      this.listPlans.forEach((plan, index) => {
        this.apiService.getImg(plan.url)
        .then(imageUrl => {
          this.listPlans[index].url = imageUrl;
        });
      });
    });
  }

  ngOnInit() {
    this.getParamns();
    this.getAthlete();
    this.getPlans();
  }
}



