import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AdminInfluencerFrmModalComponent } from '../admin-influencer-frm-modal/admin-influencer-frm-modal.component';

@Component({
  selector: 'app-admin-influencer',
  templateUrl: './admin-influencer.component.html',
  styleUrls: ['./admin-influencer.component.scss']
})
export class AdminInfluencerComponent implements OnInit {

  listAthletes: any;
  loader:boolean = false;

  constructor(
    private apiService: ApiService,
    private modalService: NgbModal,
  ) { }

  ngOnInit() {
    this.getInfluencers();
  }

  getInfluencers(){
    this.loader = true;
    this.apiService.getAthletes().then(data => {
      this.listAthletes = data;
      this.listAthletes.forEach((athlete, index) => {
        this.apiService.getImg(athlete.url)
        .then(imageUrl => {
          this.listAthletes[index].url = imageUrl;
        });
      });
      this.loader = false;
    })
    .catch(() => {
      this.loader = false;
    });
  }

  openModalFrm(influenceId) {
    let modalRef = this.modalService.open(AdminInfluencerFrmModalComponent);
    if(influenceId != 0) {      
      modalRef.componentInstance.getInfluencer(influenceId);
    }
  }

  clickDelete(id: number) {
    if(confirm("Esta seguro que desea eliminar este atleta? ")) {
      this.apiService.deleteAthlete(id);
    }
  }

}



