import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../api.service';
import { DomSanitizer } from '@angular/platform-browser';
import { AuthService } from '../auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-exercise-page',
  templateUrl: './exercise-page.component.html',
  styleUrls: ['./exercise-page.component.scss']
})
export class ExercisePageComponent implements OnInit {

  idPlan: number;
  idRoutine: number;
  idExercise: number;
  private sub: any;
  exercise: any = {name: "", series: []};
  activeUser: any = null;
  exercisesList: any = [{id: 0}];
  exercisePrev: any = {id: this.idExercise, name: ""};
  exerciseNext: any = {id: this.idExercise, name: ""};

  constructor(
    private route: ActivatedRoute,
    private apiService: ApiService,
    private sanitizer: DomSanitizer,
    private authService: AuthService,
    private router: Router,
  ) { 
  }

  ngOnInit() {
    this.getParams();
    this.getExerciseAndSeries();
    this.getExercises();
    this.authService.afAuth.authState.subscribe(currentUser => {
      if(currentUser == null){
        //this.router.navigate(['']);
      } else {
        this.activeUser = currentUser;
      }
    });    
  }

  getParams(){
    this.sub = this.route.params.subscribe(params => {
      this.idPlan = + params['idPlan'];
      this.idRoutine = + params['idRoutine'];
      this.idExercise = + params['idExercise'];
    });
  }

  getExercises(){
    this.apiService.getExercisesByRoutine(this.idRoutine)
    .then(data => {
      this.exercisesList = data;
    });
  }

  getExerciseAndSeries(){
    this.apiService.getExercisesById(this.idExercise)
    .then(data => {
      this.exercise = data;
      this.apiService.getImg(this.exercise.urlImage)
      .then(imageUrl => {
        this.exercise.urlImage = imageUrl;
      });
      this.exercise.urlVideo = this.sanitizer.bypassSecurityTrustHtml(this.exercise.urlVideo);
    });
  }
  
  prevRoutine(){
    for(let i = 0; i < this.exercisesList.length; i++){
      if(i > 0){
        if(this.exercisesList[i].id == this.idExercise){
          let routerLink=`./plan/${this.idPlan}/myplan/routine/${this.idRoutine}/current-routine/exercise/${this.exercisesList[i-1].id}/${this.exercisesList[i-1].name.split(' ').join('-').toLowerCase()}`;
          //this.router.navigateByUrl(routerLink);
          window.location.href = routerLink;
          break;
        }
      }
    }    
  }

  nextRoutine(){
    for(let i = 0; i < this.exercisesList.length; i++){
      if(i < (this.exercisesList.length - 1)){
        if(this.exercisesList[i].id == this.idExercise){
          let routerLink=`./plan/${this.idPlan}/myplan/routine/${this.idRoutine}/current-routine/exercise/${this.exercisesList[i+1].id}/${this.exercisesList[i+1].name.split(' ').join('-').toLowerCase()}`;
          //this.router.navigateByUrl(routerLink);
          window.location.href = routerLink;
          break;
        }
      }
    } 
  }

  endRoutine() {
    if(this.activeUser){
      if(confirm("¿Desea finalizar la rutina?")) {
        let end = { "Cus_Id":this.activeUser.uid,"Cup_Id": this.idPlan,"Rou_Id": this.idRoutine};
        this.apiService.postEndRoutine(end)
        .then(result => {
          this.router.navigate(['/plan/'+this.idPlan+'/myplan']);
          return result;
        })
        .catch(error => {
          alert(error);
          return false;
        });
      }
    } else {
      this.router.navigate(['/plan/'+this.idPlan+'/myplan']);
    }
  }

}



