import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { AuthService } from '../auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-landing-page',
  templateUrl: './landing-page.component.html',
  styleUrls: ['./landing-page.component.scss']
})
export class LandingPageComponent implements OnInit {

  phone : number = null;
  recaptchaVerifier: any;
  confirmationResult: any;
  countries: any = [{code: '57', name: "Colombia", id: 12}];
  indCode: any;
  codeverification: string;
  valideStatus: boolean = false;

  constructor(
    private apiService: ApiService,
    private authService: AuthService,
    private router: Router,
  ) { }

  ngOnInit() {
    this.loadCountries();
  }

  initCaptcha(){
    this.authService.initCaptcha('frm-register-captcha')
    .then(captcha => {
      this.recaptchaVerifier = captcha;
    });
  }

  ngAfterViewInit(){
    this.initCaptcha();
  }

  validCode():boolean {
    let valid = true;
    if(this.codeverification.toString().length == 5) {
      this.codeverification = "0" + this.codeverification.toString();
    }
    if(this.codeverification.toString().length != 6)
    {
      alert("Parece que el código esta erróneo. Debe ser de 6 dígitos, no " + this.codeverification.toString().length);
      valid = false;
    }
    return valid;
  }

  validateCode(){
    if(this.validCode()) {
      if(this.codeverification.toString().length == 5) {
        this.codeverification = "0" + this.codeverification.toString();
      }
      this.authService.verifyLoginCode(this.confirmationResult, this.codeverification.toString(), "", "", "")
      .then(userFire => {
        if(userFire['isNewUser'])
        {
          this.createUser(userFire);
        } else {
          this.apiService.getUserStatus(userFire["uid"])
          .then(() => {
            this.subscribe(userFire);
          })
          .catch(() => {
            this.createUser(userFire);
          });          
        }
      }) 
      .catch(error => {
        if(error.code == "auth/account-exists-with-different-credential")
        {
          alert("Ya existe una cuenta con estos datos.");
        } else if (error.code == "auth/invalid-verification-code"){
          alert("El código de validación es inválido, verifíquelo e intente de nuevo.");
        } else {
          alert("Error: " + error.message);
        }
      });
    }
  }

  loadCountries() {
    this.apiService.getCountries()
    .then(data => {
      this.countries = data;
      let tempCountries = [];
      this.countries.forEach(function(element, index) {
        if (element.id == 18 || element.id == 12) {
          tempCountries.push(element);
        }
      });
      this.countries = tempCountries;
      this.indCode = this.countries[0];
    });
  }

  sendMsm(){    
    if(this.phone){
      let completeNumber = "+" + this.indCode.code + this.phone;
      this.authService.sendLoginCode(this.recaptchaVerifier, completeNumber)
      .then(data => {
        this.confirmationResult = data;
      })
      .catch(error => {
        console.error('Error send message from primary');
      });
    }      
  }

  createUser(userFire){
    this.apiService.createUser(userFire['uid'], userFire['phoneNumber'], 
      typeof userFire['displayName'] != undefined &&  userFire['displayName'] != '' && userFire['displayName'] != null ? userFire['displayName'] : userFire['phoneNumber'], 
      typeof userFire['email'] != undefined &&  userFire['email'] != '' && userFire['email'] != null ? userFire['email'] : userFire['phoneNumber'], "", 
      1, 50, 150, true, 1, "http://influencemeapp.com/landing", 1, 1, 1, 0)
    .then(resultApi => {
      this.subscribe(userFire);
    });
  }

  subscribe(userFire) {
    this.apiService.postSubscription(userFire['uid'], 
      typeof userFire['email'] != undefined &&  userFire['email'] != '' && userFire['email'] != null ? userFire['email'] : userFire['phoneNumber'], userFire['uid'], userFire['phoneNumber'], 
      typeof userFire['displayName'] != undefined &&  userFire['displayName'] != '' && userFire['displayName'] != null ? userFire['displayName'] : userFire['phoneNumber'])
    .then(result => {
      alert("Se ha suscrito satisfactoriamente. Por favor ahora seleccione un plan");
      this.router.navigate(['/plans']);
    })
    .catch(error => {
      alert(error);
    });
  }

  clickButton(){
    if(this.valideStatus){
      this.validateCode();
    } else {
      this.sendMsm();
      this.valideStatus = true;
    }
  }
}


