import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-routine-box',
  templateUrl: './routine-box.component.html',
  styleUrls: ['./routine-box.component.scss']
})
export class RoutineBoxComponent implements OnInit {

  @Input() imgUrl: string;
  @Input() title: string;
  @Input() planId: number;
  @Input() planName: string;
  @Input() routineId: number;
  @Input() routineName: string;
  @Input() lock: boolean;
  @Input() uid: string;
  @Input() customerPlanId: number;

  constructor(
    private router: Router,
    private apiService: ApiService,
  ) {
  }

  validateDay(){
    this.apiService.getValidationRoutinesDay(this.uid, this.customerPlanId, this.routineId)
    .then(result => {
      let res : any = result;
      if(res.id == 1){
        let url = `/plan/${this.planId}/${this.planName.split(' ').join('-').toLowerCase()}/routine/${this.routineId}/${this.routineName.split(' ').join('-').toLowerCase()}`
        this.router.navigate([url]);
      } else {
        alert(res.state);
      }
    })
    .catch(error => {
      console.error(error);
    });
  }

  ngOnInit() {
  }

  loadRutinePage() {
    if(!this.lock){
      if(this.uid && this.customerPlanId && this.routineId && typeof this.uid != undefined){
        this.validateDay();
      } else {
        let url = `/plan/${this.planId}/${this.planName.split(' ').join('-').toLowerCase()}/routine/${this.routineId}/${this.routineName.split(' ').join('-').toLowerCase()}`
        this.router.navigate([url]);
      }
    } else {
      alert('No puedes acceder a este contenido aún');
    }
  }

}



