import { Component, OnInit } from '@angular/core';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TelephoneConfirmModalComponent } from '../telephone-confirm-modal/telephone-confirm-modal.component';
import { AuthService } from '../auth.service';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-telephone-modal',
  templateUrl: './telephone-modal.component.html',
  styleUrls: ['./telephone-modal.component.scss']
})
export class TelephoneModalComponent implements OnInit {

  recaptchaVerifier: any;
  phone: string;
  countries: any = [{code: '57', name: "Colombia", id: 12}];
  operators: any = [];
  indCode: any;
  confirmationResult: any;
  operator: number = 0;
  showAlert: string;
  fromFacebook: boolean = false;
  tos;

  constructor(
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private authService: AuthService,
    private apiService: ApiService,
  ) {
  }

  ngOnInit() {
    this.loadCountries();
    this.loadOperators();
  }

  loadCountries() {
    this.apiService.getCountries()
    .then(data => {
      this.countries = data;
      let tempCountries = [];
      this.countries.forEach(function(element, index) {
        if (element.id == 18 || element.id == 12) {
          tempCountries.push(element);
        }
      });
      this.countries = tempCountries;
      this.indCode = this.countries[0];
    });
  }

  callFromFacebook() {
    this.fromFacebook = true;
  }

  initCaptcha(){
    this.authService.initCaptcha('frm-register-captcha')
    .then(captcha => {
      this.recaptchaVerifier = captcha;
    });
  }

  ngAfterViewInit(){
    this.initCaptcha();
  }

  openModalTelephoneConfirm(){
    let completeNumber = "+" + this.indCode.code + this.phone;
    this.activeModal.close('Close click');
    let modalRef = this.modalService.open(TelephoneConfirmModalComponent);
    modalRef.componentInstance.setConfirmationResult(this.confirmationResult);
    modalRef.componentInstance.setOperator(this.operator);
    modalRef.componentInstance.setTelephone(completeNumber);
  }

  loadOperators() {
    this.apiService.getOperators()
    .then(data => {
      this.operators = data;      
    });
  }

  validateForm():boolean {
    this.showAlert = null;
    let valid = true;
    if(!this.indCode.code || !this.phone || this.phone.length < 8 || !this.operator || this.operator == 0) {
      valid = false;
      this.showAlert = "Parece que hay campos vacíos o inválidos."
    }
    return valid;
  }

  sendMsm(){    
    if(this.validateForm()){
      let completeNumber = "+" + this.indCode.code + this.phone;
      if(this.fromFacebook)
      {
        this.authService.linkWithPhoneNumber(completeNumber, this.recaptchaVerifier)
        .then(data => {
          this.confirmationResult = data;
          this.openModalTelephoneConfirm();
        })
        .catch(error => {
          console.error('Error send message from link');
        });
      } else {
        this.authService.sendLoginCode(this.recaptchaVerifier, completeNumber)
        .then(data => {
          this.confirmationResult = data;
          this.openModalTelephoneConfirm();
        })
        .catch(error => {
          console.error('Error send message from primary');
        });
      }      
    }
  }

  openTos() {
    window.open('http://influencemeapp.com/tos', '_blank');
  }

}



