import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-influencers',
  templateUrl: './influencers.component.html',
  styleUrls: ['./influencers.component.scss']
})
export class InfluencersComponent implements OnInit {

  listAthletes: any;

  constructor(
    private apiService: ApiService,
  ) {

  }

  ngOnInit() {
    this.apiService.getAthletes().then(data => {
      this.listAthletes = data;
      this.listAthletes.forEach((athlete, index) => {
        this.apiService.getImg(athlete.url)
        .then(imageUrl => {
          this.listAthletes[index].url = imageUrl;
        });
      });
    });
  }

}



