import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { AuthService } from '../auth.service';
import { Page } from '../page';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { RegisterModalComponent } from '../register-modal/register-modal.component';
import { AdsModalComponent } from '../ads-modal/ads-modal.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  user = null;
  listAthletes: any;
  pageRegister: Page = { id: 5, name: "Register", url: "/register" };
  activeUser: any = null;

  constructor(
    private apiService: ApiService,
    private authService: AuthService,
    private modalService: NgbModal,
  ) { 
    this.authService.afAuth.authState.subscribe(currentUser => {
      this.activeUser = currentUser;
    });
  }

  ngOnInit() {
    this.apiService.getAthletes().then(data => {
      this.listAthletes = data;
      this.listAthletes.forEach((athlete, index) => {
        this.apiService.getImg(athlete.url)
        .then(imageUrl => {
          this.listAthletes[index].url = imageUrl;
        });
      });
    });
    this.openModalAds();
  }

  openModalRegister() {
    let modalRef = this.modalService.open(RegisterModalComponent);
  }

  openModalAds(){
    let modalRef = this.modalService.open(AdsModalComponent);
  }

}



