import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../api.service';
import { AuthService } from '../auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-routine-page',
  templateUrl: './routine-page.component.html',
  styleUrls: ['./routine-page.component.scss']
})
export class RoutinePageComponent implements OnInit {

  idPlan: number;
  namePlan: string;
  idRoutine: number;
  sub: any;
  exercises: any;
  routine: any = {name: "", urlImagen: ""};
  activeUser: any = null;
  lockContent: boolean = true;
  activePlan: any = {idPlan: -1};
  statusRoutine: number = 1;
  planState: any = {id: 0};
  showErrorAlert: string = null;
  showSuccessAlert: string = null;

  constructor(
    private route: ActivatedRoute,
    private apiService: ApiService,
    private authService: AuthService,
    private router: Router,
  ) { 
    this.authService.afAuth.authState.subscribe(currentUser => {
      this.activeUser = currentUser;
      if(this.activeUser != null){
        if(typeof this.activeUser['uid'] != 'undefined'){
          this.getPlanUser();
          this.getPlanState();
        } else {
          this.activePlan = {idPlan: -1};
        }
      } else {
        this.activePlan = {idPlan: -1};
      }
    });
  }

  getPlanUser() {
    this.apiService.getPlanProgressByUser(this.activeUser.uid)
    .then(result => {
      this.activePlan = result;
    });
  }

  getPlanState() {
    this.apiService.getPlanByUser(this.activeUser.uid)
    .then(result => {
      this.planState = result;
    });
  }

  ngOnInit() {
    this.getParams();
    this.getRoutine();
    this.getExercises();
  }

  ngAfterViewInit(){
    if(this.activeUser){
      this.lockContent = !this.getUserstatus();
    }
  }

  getParams(){
    this.sub = this.route.params.subscribe(params => {
      this.idPlan = + params['idPlan'];
      this.namePlan = params['namePlan'];
      this.idRoutine = + params['idRoutine'];
    });
  }

  getExercises(){
    this.apiService.getExercisesByRoutine(this.idRoutine)
    .then(data => {
      this.exercises = data;
      this.exercises.forEach((exercise, index) => {
        this.apiService.getImg(exercise.urlImage)
        .then(imageUrl => {
          this.exercises[index].urlImage = imageUrl;
        });
      });
    });
  }

  getRoutine(){
    this.apiService.getRoutineById(this.idRoutine)
    .then(data => {
      this.routine = data;
      this.apiService.getImg(this.routine.urlImagen)
      .then(imageUrl => {
        this.routine.urlImagen = imageUrl;
      });
    });
  }

  getUserstatus() {
    if(this.activeUser.uid){
      this.apiService.getUserStatus(this.activeUser.uid)
      .then(result => {
        return result;
      })
      .catch(() => {
        return false;
      })
    } else {
      return false;
    }
  }

  initRoutine() {
    if(this.activeUser){
      let start = { "Cus_Id":this.activeUser.uid,"Cup_Id": this.planState.id,"Rou_Id": this.idRoutine};
      this.apiService.postInitRoutine(start)
      .then(result => {
        this.statusRoutine = 2;
        let routerLink=`./plan/${this.idPlan}/myplan/routine/${this.idRoutine}/current-routine/exercise/${this.exercises[0].id}/${this.exercises[0].name.split(' ').join('-').toLowerCase()}`;
        window.location.href = routerLink;
        return result;
      })
      .catch(error => {
        this.showErrorAlert = error;
        return false;
      });
    } else {
      let routerLink=`./plan/${this.idPlan}/myplan/routine/${this.idRoutine}/current-routine/exercise/${this.exercises[0].id}/${this.exercises[0].name.split(' ').join('-').toLowerCase()}`;
      window.location.href = routerLink;
    }
  }

  endRoutine() {
    if(this.activeUser){
      if(confirm("¿Desea finalizar la rutina?")) {
        let end = { "Cus_Id":this.activeUser.uid,"Cup_Id": this.planState.id,"Rou_Id": this.idRoutine};
        this.apiService.postEndRoutine(end)
        .then(result => {
          this.router.navigate(['plan/'+this.idPlan+'/'+this.namePlan]);
          return result;
        })
        .catch(error => {
          this.showErrorAlert = error;
          return false;
        });
      }
    } else {
      this.router.navigate(['plan/'+this.idPlan+'/'+this.namePlan]);
    }
  }

}



