import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';

@Injectable()
export class AuthService {

  currentUser: firebase.User = null;

  constructor(public afAuth: AngularFireAuth) {
    this.afAuth.authState.subscribe(auth => {
      this.currentUser = auth;
    });
  }

  getUser(){
    return this.currentUser;
  }

  loginWithEmailAndPassword(email: string, password: string) {
    let promise = new Promise((resolve, reject) => {
      this.afAuth.auth.signInWithEmailAndPassword(email, password)
      .then(result => {
        console.log(result);
        resolve(result);
      })
      .catch(error => {
        console.error(error);
        reject(error);
      });
    });
    return promise;
  }

  linkFacebook() {
    let promise = new Promise((resolve, reject) => {
      let provider = new firebase.auth.FacebookAuthProvider();
      firebase.auth().signInWithPopup(provider)
      .then(result => {
        console.log(result);
        resolve(result);
      })
      .catch(error => {
        console.error(error);
        reject(error);
      });
    });
    return promise;
  }

  initCaptcha(controlId){
    let promise = new Promise((resolve, reject) => {
      let captcha = new firebase.auth.RecaptchaVerifier(controlId, {
        'size': 'invisible'
      });
      captcha.render()
      .then(function(widgetId) {
        resolve(captcha);
      }).catch(error => {
        reject(error);
      });
    });
    return promise;
  }

  sendLoginCode(captcha, phoneNumber: string) {
    let promise = new Promise((resolve, reject) => {
      firebase.auth().signInWithPhoneNumber(phoneNumber, captcha)
      .then(confirmationResult => {
        console.log(confirmationResult);
        resolve(confirmationResult);
      }).catch(error => {
        console.error(error);
        reject(error);
      });
    });
    return promise;
  }

  updateProfileFirebase(userName: string, userEmail: string, userPassword :string) {
    let promise = new Promise((resolve, reject) => {
      firebase.auth().currentUser.updateProfile({displayName: userName, photoURL: ""})
      .then(() => {
        firebase.auth().currentUser.updateEmail(userEmail)
        .then(() => {
          if(userPassword){
            firebase.auth().currentUser.updatePassword(userPassword)
            .then(() => {
              resolve(firebase.auth().currentUser);
            })
            .catch((error) => {
              console.error(error);
              reject(error);
            });
          } else {
            resolve(firebase.auth().currentUser);
          }
        })
        .catch((error) => {
          console.error(error);
          reject(error);
        });
      })
      .catch((error) => {
        console.error(error);
        reject(error);
      });
    });
    return promise;
  }

  verifyLoginCode(confirmationResult, verificationCode, userName: string, userEmail: string, userPassword :string) {
    let promise = new Promise((resolve, reject) => {
      confirmationResult.confirm(verificationCode)
      .then(function (result) {
        result.user.isNewUser = result.additionalUserInfo.isNewUser;
        if(userName && userEmail && userPassword)
        {
          this.updateProfileFirebase(userName, userEmail, userPassword)
          .then(() => {
            resolve(result.user);
          })
        } else {
          resolve(result.user);
        }
      })
      .catch((error) => {
        console.error(error);
        reject(error);
      });
    });
    return promise;
  }

  linkWithPhoneNumber(phoneNumber: string, recaptchaVerifier)
  {
    let promise = new Promise((resolve, reject) => {
      firebase.auth().currentUser.linkWithPhoneNumber(phoneNumber, recaptchaVerifier)
      .then((confirmationResult) => {
        console.log(confirmationResult);
        resolve(confirmationResult);
      })
      .catch((error) => {
        console.error(error);
        reject(error);
      });
    });
    return promise;
  }

  updatePassword(password: string) {
    let promise = new Promise((resolve, reject) => {
      firebase.auth().currentUser.updatePassword(password)
      .then(res => {
        resolve(res);
      })
      .catch((error) => {
        console.error(error);
        reject(error);
      });
    });
    return promise;
  }

  logout() {
    console.log("Bye user!");
    this.afAuth.auth.signOut()
    .then((data) => {
      console.log(data);
    }).catch(error => {
      console.error(error);
    });
  }

  sendMailResetPassword(emailreset: string){
    let promise = new Promise((resolve, reject) => {
      this.afAuth.auth.sendPasswordResetEmail(emailreset)
      .then(res => {
        console.log(res);
      }).catch(error => {
        console.error(error);
      });
    });
    return promise;
  }

}



