import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ApiService } from '../api.service';
import { Plan } from '../plan';

@Component({
  selector: 'app-admin-plan-frm-modal',
  templateUrl: './admin-plan-frm-modal.component.html',
  styleUrls: ['./admin-plan-frm-modal.component.scss']
})
export class AdminPlanFrmModalComponent implements OnInit {

  plan: Plan = new Plan();
  listAthletes: any = [];
  isEdit: boolean = false;

  constructor(
    public activeModal: NgbActiveModal,
    private apiService: ApiService,
  ) { }

  ngOnInit() {
    this.getInfluencers();
  }

  getPlan(planId: number) {
    this.isEdit = true;
    this.apiService.getPlanById(planId)
    .then(data => {
      Object.assign(this.plan, data);
    });
  }

  getInfluencers(){
    this.apiService.getAthletes().then(data => {
      this.listAthletes = data;
    });
  }

  updateSave(){
    if(this.isEdit) {
      this.apiService.putPlan(this.plan);
    } else {
      this.apiService.postPlan(this.plan);
    }
  }

}



