import { Component, OnInit } from '@angular/core';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TelephoneModalComponent } from '../telephone-modal/telephone-modal.component';
import { AuthService } from '../auth.service';
import { ApiService } from '../api.service';
import { error } from 'util';

@Component({
  selector: 'app-register-modal',
  templateUrl: './register-modal.component.html',
  styleUrls: ['./register-modal.component.scss']
})
export class RegisterModalComponent implements OnInit {

  showFrmEmail: boolean = false;
  loginemail: string;
  loginpassword: string;
  showAlert: string;

  constructor(
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private authService: AuthService,
    private apiService: ApiService,
  ) { }

  ngOnInit() {
  }

  openModalTelephone(){
    this.activeModal.close();
    let modalRef = this.modalService.open(TelephoneModalComponent);
  }

  loginWithFacebook(){
    this.authService.linkFacebook()
    .then(responseFacebook => {
      if(responseFacebook["additionalUserInfo"].isNewUser || responseFacebook["user"].phoneNumber == null) {
        let userFacebook = responseFacebook["user"];
        if(userFacebook.phoneNumber == null){
          this.activeModal.close();
          let modalRef = this.modalService.open(TelephoneModalComponent);
          modalRef.componentInstance.callFromFacebook();
        } else {        
          this.apiService.createUser(userFacebook.uid, userFacebook.phoneNumber, userFacebook.displayName, userFacebook.email,"" 
            , 1, 0, 0, true, 1, "Facebook", 1,1,1, 0)
          .then(responseUserC => {
          })
          .catch(error => {
          });
        }
      }
      this.activeModal.close();
    })
    .catch(error => {
      if(error.code == "auth/account-exists-with-different-credential")
      {
        this.showAlert = "Ya existe una cuenta con estos datos.";
      }
    });
  }

  loginWithEmailAndPassword(){
    this.authService.loginWithEmailAndPassword(this.loginemail, this.loginpassword)
    .then(() => {
      this.activeModal.close();
    })
    .catch(error => {
      this.showAlert = "Usuario no encontrado. Por favor revise los datos introducidos."
    });
  }

}



