import { Component, OnInit} from '@angular/core';
import { ApiService } from '../api.service';
import { AuthService } from '../auth.service';

@Component({
  selector: 'app-plans',
  templateUrl: './plans.component.html',
  styleUrls: ['./plans.component.scss']
})
export class PlansComponent implements OnInit {

  listPlans: any;
  activeUser: any = null;
  activePlan: any = {idPlan: -1};

  constructor(
    private apiService: ApiService,
    private authService: AuthService,
  ) { 
    this.authService.afAuth.authState.subscribe(currentUser => {
      this.activeUser = currentUser;
      if(this.activeUser != null){
        if(typeof this.activeUser['uid'] != 'undefined'){
          this.getPlanUser();
        } else {
          this.activePlan = {idPlan: -1};
        }
      } else {
        this.activePlan = {idPlan: -1};
      }
    });
  }

  getPlans(){
    this.apiService.getPlans()
    .then(data => {
      this.listPlans = data;
      this.listPlans.forEach((plan, index) => {
        this.apiService.getImg(plan.url)
        .then(imageUrl => {
          this.listPlans[index].url = imageUrl;
        });
      });
    });
  }

  getPlanUser() {
    this.apiService.getPlanProgressByUser(this.activeUser.uid)
    .then(result => {
      this.activePlan = result;
    });
  }

  ngOnInit() {
    this.getPlans();
  }

}



