import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ApiService } from '../api.service';
import { Exercise } from '../exercise';

@Component({
  selector: 'app-admin-exercise-frm-modal',
  templateUrl: './admin-exercise-frm-modal.component.html',
  styleUrls: ['./admin-exercise-frm-modal.component.scss']
})
export class AdminExerciseFrmModalComponent implements OnInit {

  exercise: Exercise = new Exercise();
  isEdit: boolean = false;

  constructor(
    public activeModal: NgbActiveModal,
    private apiService: ApiService,
  ) { }

  ngOnInit() {
  }

  getExercise(exerId: number) {
    this.apiService.getExercisesById(exerId)
    .then(data => {
      Object.assign(this.exercise, data);
      console.log('getExercise');
      console.log(this.exercise);
    });
  }

  updateSave(){
    if(this.isEdit) {
      this.apiService.putExercise(this.exercise);
    } else {
      this.apiService.postExercise(this.exercise);
    }
  }

}



