import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-influencer-box',
  templateUrl: './influencer-box.component.html',
  styleUrls: ['./influencer-box.component.scss']
})
export class InfluencerBoxComponent implements OnInit {

  @Input() id: number;
  @Input() name: string;
  @Input() tags: string;
  @Input() description: string;
  @Input() imgUrl: string;
  @Input() facebook: string;
  @Input() instagram: string;
  @Input() twitter: string;

  constructor() { }

  ngOnInit() {
  }

}



