import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AdminExerciseFrmModalComponent } from '../admin-exercise-frm-modal/admin-exercise-frm-modal.component';

@Component({
  selector: 'app-admin-exercise',
  templateUrl: './admin-exercise.component.html',
  styleUrls: ['./admin-exercise.component.scss']
})
export class AdminExerciseComponent implements OnInit {

  listExercises: any;
  loader:boolean = false;

  constructor(
    private apiService: ApiService,
    private modalService: NgbModal,
  ) { }

  ngOnInit() {
    this.getExercises();
  }

  getExercises(){
    this.loader = true;
    this.apiService.getExercises().then(data => {
      this.listExercises = data;
      this.listExercises.forEach((exercise, index) => {
        this.apiService.getImg(exercise.urlImage)
        .then(imageUrl => {
          this.listExercises[index].urlImage = imageUrl;
        });
      });
      this.loader = false;
    })
    .catch(() => {
      this.loader = false;
    });
  }

  openModalFrm(exerciseId) {
    let modalRef = this.modalService.open(AdminExerciseFrmModalComponent);
    if(exerciseId != 0) {      
      modalRef.componentInstance.getExercise(exerciseId);
    }
  }

  clickDelete(id: number) {
    if(confirm("Esta seguro que desea eliminar este ejercicio? ")) {
      console.log("Implement delete functionality here");
    }
  }
}



