import { Component, OnInit } from '@angular/core';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { RegisterData1Component } from '../register-data1/register-data1.component';
import { TelephoneModalComponent } from '../telephone-modal/telephone-modal.component';
import { AuthService } from '../auth.service';
import { ApiService } from '../api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-telephone-confirm-modal',
  templateUrl: './telephone-confirm-modal.component.html',
  styleUrls: ['./telephone-confirm-modal.component.scss']
})
export class TelephoneConfirmModalComponent implements OnInit {

  confirmationResult: any;
  codeverification: string;
  telephone: string;
  operator: number;
  showAlert: string;
  activeUser: any = null;

  constructor(
    public activeModal: NgbActiveModal,
    private modalService: NgbModal,
    private authService: AuthService,
    private apiService: ApiService,
    private router: Router,
  ) { 
    this.authService.afAuth.authState.subscribe(currentUser => {
      this.activeUser = currentUser;
    });
  }

  ngOnInit() {
  }

  openModalRegister1(){
    this.activeModal.close('Close click');
    let modalRef = this.modalService.open(RegisterData1Component);
    modalRef.componentInstance.setOperator(this.operator);
  }

  openModalTelephone(){
    this.activeModal.close('Close click');
    let modalRef = this.modalService.open(TelephoneModalComponent);
  }

  /*sendMsm(){
    if(this.telephone != null){
      console.log(completeNumber);
      this.authService.sendLoginCode(this.recaptchaVerifier, completeNumber)
      .then(data => {
        this.confirmationResult = data;
        this.openModalTelephoneConfirm();
      })
      .catch(error => {
        console.error('Error send message');
      });
    }
  }*/

  setConfirmationResult(data){
    this.confirmationResult = data;
  }

  setOperator(id:number){
    this.operator = id;
  }

  setTelephone(phone:string){
    this.telephone = phone;
  }

  validCode():boolean {
    this.showAlert = null;
    let valid = true;
    if(this.codeverification.toString().length == 5) {
      this.codeverification = "0" + this.codeverification.toString();
    }
    if(this.codeverification.toString().length != 6)
    {
      this.showAlert = "Parece que el código esta erróneo. Debe ser de 6 dígitos, no " + this.codeverification.toString().length;
      valid = false;
    }
    return valid;
  }

  validateCode(){
    if(this.validCode()) {
      if(this.codeverification.toString().length == 5) {
        this.codeverification = "0" + this.codeverification.toString();
      }
      this.authService.verifyLoginCode(this.confirmationResult, this.codeverification.toString(), "", "", "")
      .then(userFire => {
        if(userFire['isNewUser'])
        {
          this.openModalRegister1();
        } else {
          this.apiService.getUserStatus(userFire["uid"])
          .then(() => {
            this.activeModal.close();
            this.router.navigate(['/my-account']);
          })
          .catch(() => {
            this.apiService.createUser(userFire['uid'], userFire['phoneNumber'], userFire['displayName'], userFire['email'], "", 
              1, 50, 150, true, 1, "http://influencemeapp.com/", 1, 1, 1, 0)
            .then(resultApi => {
              this.activeModal.close();
              this.router.navigate(['/my-account']);
            });
          });          
        }
      }) 
      .catch(error => {
        if(error.code == "auth/account-exists-with-different-credential")
        {
          this.showAlert = "Ya existe una cuenta con estos datos.";
        } else if (error.code == "auth/invalid-verification-code"){
          this.showAlert = "El código de validación es inválido, verifíquelo e intente de nuevo.";
        } else {
          this.showAlert = "Error: " + error.message;
        }
      });
    }
  }

  changeFocus(e, toFocus) {
    toFocus.focus();
  }

}



