  createUser(uid: string, phone: string, name: string, email: string, password: string, gender: number, weight: number, height: number,
    termsconditions: boolean, idOperator: number, referrer: string, typeAction: number, weightUnit: number, heightUnit: number, age: number)
  {
    let promise = new Promise((resolve, reject) => {
      phone = phone.replace('+','');
      let birthday = moment();
      if(age > 0){
        birthday = birthday.subtract(age, "years");
      }
      referrer = referrer.length > 0 ? referrer : "WebSite";
      let methodUrl = `${this.apiUrl}Customer/Insert${this.apiToken}`;
      let newUser = {
        id            : uid,
        Action        : typeAction, // 1 insert y 2 update
        Phone         : phone,
        First_name    : name,
        Last_name     : name,
        EMail         : email,
        User          : email,
        type          : 1, // 1 SMS | 2 FB | 3 GMAIL | 4 CORREO
        CreationTime  : moment().format("YYYY-MM-DD"),
        LastSignInTime: moment().format("YYYY-MM-DD"),
        Token         : uid,
        Birthday      : birthday.format("YYYY-MM-DD"),
        Password      : password,
        Gty_Id        : gender, //1 masculino 2 femenino
        UrlAvatar     : 6, // img por FB
        Set_Id        : 4, //tipo de inicio de session: 1 SMS 2 Facebook 3 Gmail 4 Email
        Weight            : weight,
        Height            : height,
        TermsAndConditions: termsconditions,
        Idoperator        : idOperator,
        MAC_Address       : "WEB Site",
        Url_Referrer      : referrer,
        sty_Id: 2,
        sst_Id: 3,
        uwt_Id: weightUnit,
        height_Id: heightUnit
      };
      console.log('Datos usuario:');
      console.log(JSON.stringify(newUser));
      this.http.post<ItemsResponse>(methodUrl, newUser)
      .toPromise()
      .then(
        res => {
          if(res.error.value == -1)
          {
            console.error("value -1");
            console.error(res.error.message);
            reject(res.error.message);
          }
          console.log("user create in DB");
          resolve(res);
        },
        msg => {
          console.error("Error message: No create user");
          console.error(msg);
          reject(msg);
        }
      );
    });
    return promise;
  }

