import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { ApiService } from '../api.service';
import { Router } from '@angular/router';
import { NgbModule, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ChangePasswordModalContentComponent } from '../change-password-modal-content/change-password-modal-content.component';
import * as moment from 'moment';

@Component({
  selector: 'app-my-account',
  templateUrl: './my-account.component.html',
  styleUrls: ['./my-account.component.scss']
})
export class MyAccountComponent implements OnInit {

  activeUser: any = {displayName: "", email: "", phoneNumber: "", country: 12, gender: 1, age: 0, 
    height: 0, weight: 0, idoperator: 66, uwt_Id: 2, height_Id: 1};
  showAlertMail: string;
  showAlert: string;
  catSubscriptionStatus: any = [];
  countries: any = [{code: '57', name: "Colombia", id: 12}];
  catWeight: any = [];
  catHeight: any = [];
  catGender: any = [];
  catOperators: any = [];

  constructor(
    private authService: AuthService,
    private router: Router,
    private modalService: NgbModal,
    private apiService: ApiService,
  ) {
    this.authService.afAuth.authState.subscribe(currentUser => {
      if(currentUser == null){
        this.router.navigate(['']);
      } else {
        this.activeUser = currentUser;
        this.loadUser();
      }
    });
  }

  loadUser() {
    this.apiService.getUserById(this.activeUser.uid)
    .then(dataUser => {
      this.activeUser.country = 12;
      this.activeUser.gender = dataUser["gty_Id"];
      this.activeUser.age = moment().diff(moment(dataUser["birthday"]).format("YYYY-MM-DD"), 'years', false);
      this.activeUser.height = dataUser["height"];
      this.activeUser.weight = dataUser["weight"];
      this.activeUser.sst_Id = dataUser["sst_Id"];
      this.activeUser.idoperator = dataUser["idoperator"];
      this.activeUser.uwt_Id = dataUser["uwt_Id"];
      this.activeUser.height_Id = dataUser["height_Id"];
      this.loadCatOperators();
    }).catch(error => {
      this.showAlert = error;
    });
  }

  ngOnInit() {
    this.loadSubscriptionStatus();
    this.loadCountries();
    this.loadCatWeight();
    this.loadCatHeight();
    this.loadCatGender();
  }

  loadCatWeight() {
    this.apiService.getUnitWeight()
    .then(data => {
      this.catWeight = data;
    });
  }

  loadCatHeight() {
    this.apiService.getUnitHeight()
    .then(data => {
      this.catHeight = data;
    });
  }

  loadCatGender() {
    this.apiService.getGenderType()
    .then(data => {
      this.catGender = data;
    });
  }

  loadCatOperators() {
    this.apiService.getOperators()
    .then(data => {
      this.catOperators = data;
      for(let i = 0; i < this.catOperators.length ; i++){
        if(this.catOperators[i].id == this.activeUser.idoperator){
          this.activeUser.country = this.catOperators[i].idCountry;
        }
      }
    });
  }

  subscribe() {
    this.showAlertMail = null;
    this.showAlert = null;
    this.apiService.postSubscription(this.activeUser['uid'], this.activeUser['email'], this.activeUser['uid'], 
      this.activeUser['phoneNumber'], this.activeUser['displayName'])
    .then(result => {
      this.loadUser();
      this.showAlertMail = "Se ha suscrito satisfactoriamente.";
    })
    .catch(error => {
      this.showAlert = error;
    });
  }

  unsubscribe(){
    this.showAlertMail = null;
    this.showAlert = null;
    this.apiService.postUnsubscribe(this.activeUser['uid'])
    .then(() => {
      this.loadUser();
      this.showAlertMail = "Se cancelo la suscripción satisfactoriamente.";
    })
    .catch(error => {
      this.showAlert = error;
    });
  }

  loadCountries() {
    this.apiService.getCountries()
    .then(data => {
      this.countries = data;
    });
  }

  loadSubscriptionStatus() {
    this.apiService.getSuscriptionStatus()
    .then(data => {
      this.catSubscriptionStatus = data;
    });
  }

  openModalChangePass() {
    //let modalRef = this.modalService.open(ChangePasswordModalContentComponent);
    this.changePassword();
  }

  updateProfile() {
    this.showAlertMail = null;
    this.showAlert = null;
    this.apiService.createUser(this.activeUser['uid'], this.activeUser['phoneNumber'], this.activeUser['displayName'], 
      this.activeUser['email'], "", this.activeUser.gender, this.activeUser.weight, this.activeUser.height, true, this.activeUser.idoperator,
      "website" , 0, this.activeUser.uwt_Id, this.activeUser.height_Id, this.activeUser.age)
    .then(() => {
      this.showAlertMail = "Se actualizo correctamente el usuario.";
    })
    .catch(error => {
      this.showAlert = error;
    });
  }

  changePassword() {
    this.showAlertMail = null;
    this.showAlert = null;
    if(this.activeUser.email != null){
      this.authService.sendMailResetPassword(this.activeUser.email);
      this.showAlertMail = "Recibirás un correo con las instrucciones para que actualices o crees tu contraseña, revisa tu bandeja de entrada.";
    } else {
      this.showAlert = "Primero debes actualizar tu dirección de correo.";
    }
  }

}



