import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { InfluencersComponent } from './influencers/influencers.component';
import { PlansComponent } from './plans/plans.component';
import { RegisterComponent } from './register/register.component';
import { InfluencerPageComponent } from './influencer-page/influencer-page.component';
import { PlanPageComponent } from './plan-page/plan-page.component';
import { RoutinePageComponent } from './routine-page/routine-page.component';
import { ExercisePageComponent } from './exercise-page/exercise-page.component';
import { MyAccountComponent } from './my-account/my-account.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { PrivacyPolicyComponent } from './privacy-policy/privacy-policy.component';
import { TosComponent } from './tos/tos.component';
import { AdminComponent } from './admin/admin.component';
import { LoginAdminComponent } from './login-admin/login-admin.component';
import { LandingPageComponent } from './landing-page/landing-page.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'influencers', component: InfluencersComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'plans', component: PlansComponent },
  { path: 'influencer/:id/:name', component: InfluencerPageComponent },
  { path: 'plan/:id/:name', component: PlanPageComponent },
  { path: 'plan/:idPlan/:namePlan/routine/:idRoutine/:nameRoutine', component: RoutinePageComponent },
  { path: 'plan/:idPlan/:namePlan/routine/:idRoutine/:nameRoutine/exercise/:idExercise/:nameExercise', component: ExercisePageComponent },
  { path: 'my-account', component: MyAccountComponent },
  { path: 'privacy', component: PrivacyPolicyComponent },
  { path: 'tos', component: TosComponent },
  { path: 'admin', component: AdminComponent },
  { path: 'admin/login', component: LoginAdminComponent },
  { path: 'landing', component: LandingPageComponent },
  { path: '**', component: PageNotFoundComponent },
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ],
  declarations: []
})
export class AppRoutingModule { }


