import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ApiService } from '../api.service';
import { Routine } from '../routine';

@Component({
  selector: 'app-admin-routines-frm-modal',
  templateUrl: './admin-routines-frm-modal.component.html',
  styleUrls: ['./admin-routines-frm-modal.component.scss']
})
export class AdminRoutinesFrmModalComponent implements OnInit {

  routine: Routine = new Routine();
  listPlans: any = [];
  isEdit: boolean = false;

  constructor(
    public activeModal: NgbActiveModal,
    private apiService: ApiService,
  ) { }

  ngOnInit() {
    this.getPlans();
  }

  getRoutine(routineId: number) {
    this.isEdit = true;
    this.apiService.getRoutineById(routineId)
    .then(data => {
      Object.assign(this.routine, data);
    });
  }

  getPlans(){
    this.apiService.getPlans().then(data => {
      this.listPlans = data;
    });
  }

  updateSave(){
    if(this.isEdit) {
      this.apiService.putRoutine(this.routine);
    } else {
      this.apiService.postRoutine(this.routine);
    }
  }

}



