import { Component } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { AuthService } from '../auth.service';
import { FormsModule }   from '@angular/forms';
import { User } from '../user';

@Component({
  selector: 'app-login-modal-content',
  templateUrl: './login-modal-content.component.html',
  styleUrls: ['./login-modal-content.component.scss']
})
export class LoginModalContentComponent {

  logUser: User = {email: "", password: "", phone: ""};
  showAlert: boolean = false;
  
  constructor(
    public activeModal: NgbActiveModal,
    private authService: AuthService,
  ) { 
    
  }

  loginWithEmailAndPassword(){
    this.authService.loginWithEmailAndPassword(this.logUser.email, this.logUser.password)
    .then(() => {
      this.showAlert = false;
      this.activeModal.close();
    })
    .catch(error => {
      this.showAlert = true;
    });
  }

  loginWithFacebook(){
    this.authService.linkFacebook()
    .then(() => {
      this.activeModal.close();
    })
    .catch(error => {
      this.showAlert = true;
    });
  }

}



