import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { ApiService } from '../api.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register-data2',
  templateUrl: './register-data2.component.html',
  styleUrls: ['./register-data2.component.scss']
})
export class RegisterData2Component implements OnInit {

  objUser: any;
  catWeight: any = [];
  catHeight: any = [];
  catGender: any = [];
  showErrorAlert: string = null;

  constructor(
    private authService: AuthService,
    private apiService: ApiService,
    public activeModal: NgbActiveModal,
    private router: Router,
  ) { }

  ngOnInit() {
    this.loadCatWeight();
    this.loadCatHeight();
    this.loadCatGender();
  }

  loadCatWeight() {
    this.apiService.getUnitWeight()
    .then(data => {
      this.catWeight = data;
    });
  }

  loadCatHeight() {
    this.apiService.getUnitHeight()
    .then(data => {
      this.catHeight = data;
    });
  }

  loadCatGender() {
    this.apiService.getGenderType()
    .then(data => {
      this.catGender = data;
    });
  }

  updateCreateProfile() {
    this.showErrorAlert = null;
    this.authService.updateProfileFirebase(this.objUser.name, this.objUser.email, null)
    .then(user => {
      this.apiService.createUser(user['uid'], user['phoneNumber'], this.objUser.name, this.objUser.email, "", 
      this.objUser.gender, this.objUser.weight, this.objUser.height, true, this.objUser.operator, "website", 1, 
      this.objUser.weightUnit, this.objUser.heightUnit, 0)
      .then(() => {
        this.router.navigate(['/plans']);
      })
      .catch(error => {
        this.showErrorAlert = error;
      });
    })
    .catch(error => {
      this.showErrorAlert = error.message;
    });
  }

  createUserCloseModal() {
    this.updateCreateProfile();
    this.activeModal.close('Close click');
  }

  setObjUser(obj) {
    this.objUser = obj;
  }

}



