import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../api.service';
import { DomSanitizer } from '@angular/platform-browser';
import { AuthService } from '../auth.service';

@Component({
  selector: 'app-plan-page',
  templateUrl: './plan-page.component.html',
  styleUrls: ['./plan-page.component.scss']
})
export class PlanPageComponent implements OnInit {

  id: number;
  private sub: any;
  plan: any = {name: "", url: ""};
  routines: any;
  activeUser: any = null;
  lockContent: boolean = false;
  activePlan: any = {idPlan: -1, daysCompleted: 0};
  showErrorAlert: string = null;
  showSuccessAlert: string = null;
  planState: any = {id: 0};

  constructor(
    private route: ActivatedRoute,
    private apiService: ApiService,
    private sanitizer: DomSanitizer,
    private authService: AuthService,
  ) { 
    this.authService.afAuth.authState.subscribe(currentUser => {
      this.activeUser = currentUser;
      if(this.activeUser != null){
        if(typeof this.activeUser['uid'] != 'undefined'){
          this.getPlanUser();
          this.getPlanState();          
        } else {
          this.activeUser.uid = null;
          this.activePlan = {idPlan: -1, daysCompleted: 0};
          this.lockContent = true;
        }
      } else {
        this.activePlan = {idPlan: -1, daysCompleted: 0};
        this.lockContent = true;
      }
    });
  }

  getPlanUser() {
    this.apiService.getPlanProgressByUser(this.activeUser.uid)
    .then(result => {
      this.activePlan = result;
      if(this.id == this.activePlan.idPlan){
        this.lockContent = false;
      }
    });    
  }

  getPlan(){
    this.apiService.getPlanById(this.id)
    .then(data => {
      this.plan = data;
        this.apiService.getImg(this.plan.url)
        .then(imageUrl => {
          this.plan.url = imageUrl;
        });
        this.plan.videoPresentation = this.sanitizer.bypassSecurityTrustHtml(this.plan.videoPresentation);
    });
  }

  ngAfterViewInit(){
    if(this.activeUser && typeof this.activeUser != undefined && typeof this.activeUser.uid != undefined){
      this.lockContent = !this.getUserstatus();
    }
  }

  getRoutines(){
    this.apiService.getRoutinesByPlan(this.id)
    .then(data => {
      this.routines = data;
      debugger;
      this.routines.forEach((routine, index) => {
        this.apiService.getImg(routine.urlImagen)
        .then(imageUrl => {
          this.routines[index].urlImagen = imageUrl;
        });
      });
    });
  }

  getParams(){
    this.sub = this.route.params.subscribe(params => {
      this.id = +params['id'];
    });
  }

  getUserstatus() {
    if(this.activeUser.uid){
      this.apiService.getUserStatus(this.activeUser.uid)
      .then(result => {
        return result;
      })
      .catch(() => {
        return false;
      })
    } else {
      return false;
    }
  }

  ngOnInit() {
    this.getParams();
    this.getPlan();
    this.getRoutines();
  }

  suscribirnew(){
    this.apiService.postSubscription(this.activeUser['uid'], this.activeUser['email'], this.activeUser['uid'], 
      this.activeUser['phoneNumber'], this.activeUser['displayName'])
    .then(() => {
      this.apiService.postChangePlan(this.activeUser.uid, this.id)
      .then(() => {
        this.showSuccessAlert = "Se ha suscrito satisfactoriamente a este plan.";
      })
      .catch(serror => {
        this.showErrorAlert = serror;
      });
    })
    .catch(error => {
      this.showErrorAlert = error;
    });
  }

  getPlanState() {
    this.showErrorAlert = null;
    this.apiService.getPlanByUser(this.activeUser.uid)
    .then(result => {
      this.planState = result;
    })
    .catch(error => {
      this.planState = {id: 0};
      this.showErrorAlert = error;
    });
  }

  initRoutine() {
    let createConnUserPlan = 0;
    if(this.planState.id == 0){
      this.apiService.postSubscription(this.activeUser['uid'], this.activeUser['email'], this.activeUser['uid'], 
        this.activeUser['phoneNumber'], this.activeUser['displayName'])
      .then(() => {
        this.apiService.setPlanUser(this.activeUser.uid, this.id)
        .then(resultP => {
          console.log(resultP);
          let start = { "Cus_Id":this.activeUser.uid,"Cup_Id": resultP[0],"Rou_Id": this.routines[0].id};
          this.apiService.postInitRoutine(start)
          .then(result => {
            this.showSuccessAlert = "Se ha suscrito satisfactoriamente a este plan.";
          })
          .catch(error => {
            this.showErrorAlert = error;
          });
        })
        .catch(error => {
          this.showErrorAlert = error;
        });
      })
      .catch(error => {
        this.showErrorAlert = error;
      });
    } else {
      this.apiService.setPlanUser(this.activeUser.uid, this.id)
      .then(resultP => {
        console.log(resultP);
        let start = { "Cus_Id":this.activeUser.uid,"Cup_Id": resultP[0],"Rou_Id": this.routines[0].id};
        this.apiService.postInitRoutine(start)
        .then(result => {
          this.showSuccessAlert = "Se ha suscrito satisfactoriamente a este plan.";
        })
        .catch(error => {
          this.showErrorAlert = error;
        });
      })
      .catch(error => {
        this.showErrorAlert = error;
      });
    }
  }

  checkStatusRoutine() {
    this.showErrorAlert = null;
    this.showSuccessAlert = null;
    this.apiService.getRoutineStatus(456,43)
    .then(result => {
      let res : any = result;
      if(res.message = "unused routine")
      {
        return true;
      } else {
        return false;
      }
    })
    .catch(error => {
      this.showErrorAlert = error;
      return false;
    });
  }

  suscribir() {
    this.showErrorAlert = null;
    this.showSuccessAlert = null;
    if(this.activeUser != null && typeof this.activeUser['uid'] != 'undefined'){
      this.apiService.getPlanProgressByUser(this.activeUser['uid'])
      .then(result => {
        if(Object.keys(result).length > 0){
          if(confirm("Esta seguro que desea cambiar de plan. Solo podrá realizar esta operación una vez? ")) {
            this.apiService.postChangePlan(this.activeUser.uid, this.id)
            .then(() => {
              this.showSuccessAlert = "Se ha suscrito satisfactoriamente a este plan.";
            })
            .catch(() => {
              this.showErrorAlert = "Parece que ya hiciste un cambio de plan.";
            });
          }
        } else {
          //this.suscribirnew();
          this.initRoutine();
        }
      })
      .catch(error => {
        this.suscribirnew();
        if(error != null && typeof error != undefined){
          console.log(error);
          this.showErrorAlert = error;
        }
      });
    } else {
      alert('Debe iniciar sesión');
    }
  }

}



