import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ApiService } from '../api.service';
import { Trainer } from '../trainer';

@Component({
  selector: 'app-admin-influencer-frm-modal',
  templateUrl: './admin-influencer-frm-modal.component.html',
  styleUrls: ['./admin-influencer-frm-modal.component.scss']
})
export class AdminInfluencerFrmModalComponent implements OnInit {

  influencer: Trainer = new Trainer();
  isEdit: boolean = false;

  constructor(
    public activeModal: NgbActiveModal,
    private apiService: ApiService,
  ) { }

  ngOnInit() {
  }

  getInfluencer(infId: number) {
    this.isEdit = true;
    this.apiService.getAthleteById(infId)
    .then(data => {
      Object.assign(this.influencer, data);
    });
  }

  updateSave(){
    if(this.isEdit) {
      this.apiService.putAthlete(this.influencer);
    } else {
      this.apiService.postAthlete(this.influencer);
    }
  }

}



